import banner from '../img/headerImg.jpg';
export default function Welcome({...capsule})
{
    return (
        <div>
            <div className="row mx-0 justify-content-center main bg-light">
                <p className="col-1 subheading">Welcome</p>
                <img src={banner} alt="excerpt pic from book cover" className="img-fluid px-0" />
            </div><br/>
                <div className="col mx-2">
                    <div className="row justify-content-center main">
                        <p className="col-3 subheading lg mt-5 mb-5">Introduction by the Author</p><br/>
                    </div>
                    <div className="row justify-content-center mb-2 main text">
                        <p className="col-5">When my children were little I told them bedtime stories most every night. 
                        One evening I created the characters Hatty and Barty and told them a story about these new fictional characters.  
                        Both of my children loved this story so much that from that night forward they would only let me tell them 
                        stories about Hatty and Barty.<br/><br/>
                        I told these Hatty and Barty stories for several years. It was quite a stretch to my creativity after a while 
                        to keep coming up with new ideas and story subject matter but somehow I always managed to do so and I always 
                        enjoyed telling them.<br/><br/>
                        Sometimes when I was tired, I would get to the crucial point in the story and then tell my children "to be 
                        continued".  Their response would be "No Dad! Finish the story!" Usually I wouldnâ€™t relent and then the next 
                        night sometimes I would forget the story I had started the night before and this would create greater 
                        consternation.<br/><br/>
                        You the reader, have one benefit that my children did not always have; and that is all of the bedtime stories 
                        in these books, created many years ago, have an ending even though some still include the infamous 
                        "to be continued" phrase in them.<br/><br/>
                        I hope that you and your children will enjoy these stories as much as my children did listening to them when 
                        they were growing up.<br/><br/>
                        Grant Boyer</p>
                    </div>
                </div>
        </div>
    );
}